<template>
  <div class="page">
    <div class="cont">
      <div class="wrap align-center space-between">
        <input
          type="text"
          placeholder="请输入2-16个字符的昵称"
          v-model="nick_name"
          class="modify-name"
          ref="inputVal"
          v-on:keyup.enter="saveName"
        />
        <div class="clear align-center">
          <img src="../assets/home/clear@2x.png" @click="clearFn" alt="" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      nick_name: JSON.parse(localStorage.getItem('userInfo')).data.nick_name,
      // nick_name: this.$store.getters.userInfo.nick_name,
      timer: null
    }
  },
  components: {
  },
  mounted () {
    this.$refs.inputVal.focus()
  },
  beforeDestroy () {
    clearTimeout(this.timer)
  },
  methods: {
    async saveName () {
      this.$store.commit('setNickname', { nick_name: this.nick_name })
      // this.Toast(`验证码发送成功`)
      this.Indicator.open({
        text: '处理中...',
        spinnerType: 'fading-circle'
      })
      let res = await this.$request('post', 'user/updatenikename', {
        uid: JSON.parse(localStorage.getItem('userInfo')).data.uid, nick_name: this.nick_name
      })
      console.log(res)
      if (res && res.error_no === '0') {
        this.$store.commit('setUserInfo', res.data)
        this.Indicator.close()
        this.Toast(`修改成功`)
        this.$router.back()
      } else {
        this.Indicator.close()
      }
      // that.timer = setTimeout(() => {
      //   this.Toast(`修改成功`)
      //   this.$router.back()
      //   this.Indicator.close()
      // }, 1000)
    },
    clearFn () {
      this.nick_name = ''
    }
  }
}
</script>

<style lang="less" scoped>
@import url("../assets/css/mixin.less");
.page {
  width: 100%;
  height: 100%;
  background-color: #ef5d56;
  .bg-image("../assets/home/user_bg","png");
  padding: 32px;
  .cont {
    width: 100%;
    height: auto;
    min-height: 100px;
    background: #fff;
    border-radius: 16px;
    .wrap {
      width: 100%;
      height: 108px;
      padding: 0 32px;
      font-size: 32px;
      font-family: PingFangSC-Regular, PingFang SC;
      color: #333333;
      .modify-name {
        width: calc(100% - 42px);
        height: 100%;
        border: none;
        outline: none;
        font-size: 32px;
        color: #333;
      }
      .clear {
        width: 32px;
        height: 100%;
        margin-left: 10px;
        > img {
          width: 32px;
          height: 32px;
        }
      }
    }
  }
}
</style>



